:root {
    --primary: #1D5B79;
    --primarylight: #468B97;
    --secondary: #EF6262;
    --secondarylight: #F3AA60;
    --bg: #ffffff;
    --font: #132C33;
}

* {
    box-sizing: border-box;
}

p {
    font-family: 'Manrope', sans-serif;
    font-size: 16px;
    line-height: 1.7;
    font-weight: lighter;
    color: #132C33;
    color: var(--font);
    margin: 0;
}

html {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
}

body {
    background-color: #ffffff;
    background-color: var(--bg);
    margin: 0;
    padding: 0;

}

a {
    font-family: 'Inter', sans-serif;
    color: #1D5B79;
    color: var(--primary);
    font-weight: bold;
    text-decoration: none;
}

a:hover {
    color: #EF6262;
    color: var(--secondary);
}

h1 {
    font-family: 'Manrope', sans-serif;
    color: #1D5B79;
    color: var(--primary);
    font-weight: bolder;
    font-size: 26px;
    margin-top: 0;
    margin-bottom: 0;
    display: inline;
}

h2 {
    font-family: 'Manrope', sans-serif;
    color: #1D5B79;
    color: var(--primary);
    font-weight: bolder;
    font-size: 16px;
    margin-top: 0;
    margin-bottom: 0;
    display: inline;
}

h3 {
    font-family: "Inter", sans-serif;
    color: #468B97;
    color: var(--primarylight);
    font-size: 20px;
    font-weight: normal;
    margin: 5px 0 0;
}

h4 {
    font-family: "Inter", sans-serif;
    color: #468B97;
    color: var(--primarylight);
    font-weight: normal;
    font-size: 14px;
    margin: 5px 0;
}

h5 {
    font-family: 'Manrope', sans-serif;
    color: #1D5B79;
    color: var(--primary);
    font-size: 16px;
}

@media screen and (max-width: 768px) {
    h1 {
        font-size: 24px;
    }

    h2 {
        font-size: 14px;
    }
}



.portfolio-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100%;
    position: relative;
}

