.right-content {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 500px;
    margin-left: 400px;
    margin-top: 80px;
}

@media screen and (max-width: 940px) {
    .portfolio-container {
        -webkit-flex-direction: column;
                flex-direction: column;
    }

    .left-content,
    .right-content {
        width: calc(100% - 50px);
        margin: 0 25px;
        box-sizing: border-box;
    }
}
.projects-content {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    margin: 40px 0 200px 0;
}

.projects-header {
    display: none;
}

@media screen and (max-width: 940px) {
    .projects-content {
        padding: 0;
        margin: 0;
    }

    .projects-header {
        display: contents;
    }
}
.project-card {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    margin-bottom: 30px;
}

.left {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    position: relative;
    width: 350px;
}

.right {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    padding: 0 0 10px 15px;
}

.project-title {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 5px;
}

.technologies {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.project-links {
    display: -webkit-flex;
    display: flex;
    margin-top: 8px;
}

.project-links a {
    text-align: center;
    color: var(--primary);
    font-size: 14px;
    padding: 2px 7px;
}

.project-links a:hover {
    color: var(--secondary);
    cursor: pointer;
    transition: 0.2s;
}

.project-img {
    width: 110px;
    height: 80px;
    box-shadow: 2px 3px 5px rgba(198, 197, 197, 0.5);
    border-radius: 4px;
}

.left:hover .project-links {
    opacity: 2;
}

@media screen and (max-width: 940px) {
    .project-card {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-align-items: flex-start;
                align-items: flex-start;
        -webkit-justify-content: flex-start;
                justify-content: flex-start;
        padding: 0;
        margin: 0;
    }

    .left {
        padding: 0;
        margin: 0 0 10px 0;
        width: 100%;
        -webkit-align-items: flex-start;
                align-items: flex-start;
    }

    .right {
        padding: 0;
        margin-bottom: 30px;
    }
}
.about-div {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 0;
    margin-bottom: 20px;
}

.about-header {
    display: none;
}

.about-div p {
    padding: 5px 0;
}

@media screen and (max-width: 940px) {
    .about-div {
        padding: 0;
        margin-bottom: 40px;
    }

    .about-header {
        display: contents;
    }
}
.left-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-right: 600px;
    margin-top: 80px;
    position: fixed;
    width: 400px;
}

.nav-links {
    margin-top: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 150px;
}

.nav-links a {
    color: var(--font);
    font-size: 16px;
    padding: 8px 0;
    border-bottom: 3px solid transparent;
}

.nav-links a:hover {
    color: var(--secondarylight);
    cursor: pointer;
    border-radius: 0;
    transition: 0.2s;
    border-bottom: 3px solid var(--primarylight);
    width: 15px;
}

.nav-links > .active {
    color: var(--primarylight);
    border-bottom: 3px solid var(--secondarylight);
    background: transparent;
    border-radius: 0;
    width: 15px;
}

.name-title-container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-content: center;
            align-content: center;
    padding: 0;
}

.name-title-column {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-left: 10px;
}

.pic img {
    width: 70px;
    height: 70px;
    border: 4px solid var(--secondary);
    border-radius: 100%;
}

.social {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin-top: 50px;
}

.social a {
    color: var(--secondary);
    font-size: 30px;
    padding: 5px;
    margin-right: 10px;
}

.social a:hover {
    color: var(--primarylight);
    padding: 5px;
    transition: 0.2s;
}

.technologies-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-top: 50px;
    width: 300px;
}

.technologies {
    display: -webkit-flex;
    display: flex;
    margin-top: 10px;
}

.interests {
    margin-top: 50px;
}

@media screen and (max-width: 940px) {
    .left-container {
        position: static;
        width: 100%;
        margin-top: 50px;
    }

    .nav-links {
        display: none;
    }

    .social,
    .technologies-container,
    .interests {
        margin-top: 20px;
    }

    .technologies-container {
        width: 100%;
    }
}
.experience-content {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    height: 400px;
    margin: 40px 0;
}

.experience-header {
    display: none;
}

.job-title {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-bottom: 2px;
}

@media screen and (max-width: 940px) {
    .experience-content {
        padding: 0;
        margin-bottom: 80px;
    }

    .experience-header {
        display: contents;
    }
}
